itir y recibir información.")])])]),_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0 col-lg-12"},[_c('h5',[_vm._v("Contextualidad")]),_c('p',[_vm._v("La comunicación siempre ocurre dentro de un contexto específico que influye en su significado y efectividad. Este contexto incluye factores como el entorno cultural, social, histórico y personal de los interlocutores. El mismo mensaje puede tener diferentes interpretaciones dependiendo de estos elementos.")])])]),_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0 col-lg-12"},[_c('h5',[_vm._v("Relacionalidad")]),_c('p',[_vm._v("La comunicación no solo transmite información, sino que también establece relaciones entre los individuos. A través de la forma en que nos comunicamos, expresamos nuestras emociones, actitudes y valores, creando o reforzando lazos de confianza, cooperación o conflicto.")])])]),_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0 col-lg-12"},[_c('h5',[_vm._v("Intencionalidad")]),_c('p',[_vm._v("Aunque parte de la comunicación puede ser no verbal o involuntaria, una gran parte de ella tiene una intención consciente, como la persuasión, la educación, el entretenimiento o la expresión de emociones. Las intenciones guían el contenido y la forma del mensaje.")])])]),_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0 col-lg-12"},[_c('h5',[_vm._v("Multidimensionalidad")]),_c('p',[_vm._v("La comunicación es compleja y tiene múltiples dimensiones. Aparte de la dimensión verbal, también está la no verbal (gestos, postura, tono de voz) que transmite emociones y actitudes, y el paralenguaje (cómo decimos algo, más que lo que decimos). Esta complejidad contribuye a que la comunicación sea una actividad rica y multifacética.")])])]),_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0 col-lg-12"},[_c('h5',[_vm._v("Proceso continuo")]),_c('p',[_vm._v("La comunicación no es un evento aislado, sino un proceso continuo y dinámico. A lo largo de la interacción, los mensajes se modifican, reinterpretan y responden. Es un flujo constante de información que evoluciona en función de las interacciones y el contexto.")])])])])],1),_vm._m(4)]),_c('separador'),_vm._m(5),_vm._m(6),_c('div',{staticClass:"row justify-content-center align-items-center mb-5",attrs:{"data-aos":"fade-up"}},[_c('div',{staticClass:"col-lg-12"},[_c('AcordionA',{attrs:{"tipo":"a","clase-tarjeta":"tarjeta tarjeta--azul"}},[_c('div',{staticClass:"row align-items-center justify-content-center",attrs:{"titulo":"Interactividad y circularidad"}},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0 col-lg-7"},[_c('p',{staticClass:"px-5"},[_vm._v("La comunicación no es un proceso unidireccional, sino más bien circular. Esto significa que tanto el emisor como el receptor tienen roles activos y pueden influir mutuamente en el curso de la comunicación. Cuando el receptor responde al mensaje del emisor, se convierte a su vez en emisor, lo que provoca una retroalimentación constante entre los participantes. Este concepto se relaciona con el modelo de Shannon y Weaver (1949), que, aunque inicialmente planteaba un modelo lineal, también reconocía la importancia de la retroalimentación como un mecanismo que permite ajustar el mensaje durante el proceso de transmisión.")])]),_c('div',{staticClass:"col-md-6 col-lg-4"},[_c('img',{attrs:{"src":require("@/assets/curso/Tema1/img05.png"),"alt":""}})])]),_c('div',{staticClass:"row align-items-center justify-content-center",attrs:{"titulo":"La influencia del medio en el individuo"}},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0 col-lg-7"},[_c('p',{staticClass:"px-5"},[_vm._v("El medio a través del cual se transmite el mensaje tiene un impacto significativo en la forma en que el individuo percibe y responde a la comunicación. Según el modelo de comunicación de la Escuela de Palo Alto, encabezado por Paul Watzlawi ck, la comunicación es un proceso continuo, y cualquier interacción siempre es una forma de comunicación, ya sea verbal o no verbal. Este enfoque subraya que el medio y el contexto (como el cultural, social y tecnológico) determinan en gran medida el significado de los mensajes, lo que sugiere que la comunicación está en constante cambio y es sensible a los diferentes factores que influyen en la interacción.")])]),_c('div',{staticClass:"col-md-6 col-lg-4"},[_c('img',{attrs:{"src":require("@/assets/curso/Tema1/img06.png"),"alt":""}})])]),_c('div',{staticClass:"row align-items-center justify-content-center",attrs:{"titulo":"Roles de emisor y receptor conceptual"}},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0 col-lg-7"},[_c('p',{staticClass:"px-5"},[_vm._v("Siguiendo la perspectiva de Quintero (1997), es fundamental comprender los roles de los interlocutores en el proceso comunicativo. El emisor es el encargado de transmitir el mensaje, mientras que el receptor lo recibe y, en muchos casos, responde a él. Sin embargo, como se mencionó antes, estos roles no son estáticos. En el modelo de comunicación propuesto por Roman Jakobson (1960), se destaca la función de la retroalimentación, que convierte al receptor en emisor en un ciclo continuo. Es a través de esta interactividad que la comunicación puede ser efectiva, ya que permite ajustes en tiempo real basados en la recepción del mensaje y las respuestas generadas.")])]),_c('div',{staticClass:"col-md-6 col-lg-4"},[_c('img',{attrs:{"src":require("@/assets/curso/Tema1/img07.png"),"alt":""}})])]),_c('div',{staticClass:"row align-items-center justify-content-center",attrs:{"titulo":"Responsabilidad y reflexividad"}},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0 col-lg-7"},[_c('p',{staticClass:"px-5"},[_vm._v("La responsabilidad del receptor en la comunicación es crucial. Según el modelo interaccionista de la comunicación de Herbert A. Simon, los participantes no solo deben emitir y recibir mensajes, sino también interpretar, filtrar y, en muchos casos, alterar la información para ajustarla a su propio contexto o perspectiva. La habilidad del receptor para reflexionar sobre el mensaje, entender su contexto y responder de manera adecuada contribuye a la efectividad de la comunicación. La reflexividad, entendida como la capacidad de reconocer y ajustar los procesos comunicativos, es vital para la construcción de sentido.")])]),_c('div',{staticClass:"col-md-6 col-lg-4"},[_c('img',{attrs:{"src":require("@/assets/curso/Tema1/img08.png"),"alt":""}})])]),_c('div',{staticClass:"row align-items-center justify-content-center",attrs:{"titulo":"El modelo de comunicación transaccional"}},[_c('div',{staticClass:"col-md-12 mb-4 mb-md-0 col-lg-7"},[_c('p',{staticClass:"px-5"},[_vm._v("En lugar de ver la comunicación como una secuencia de pasos estáticos (emisor - mensaje - receptor), el modelo transaccional de Barnlund (1970) propone que los interlocutores están continuamente enviando y recibiendo mensajes al mismo tiempo, sin una distinción clara entre emisor y receptor. Ambos participan activamente en la creación del significado, lo que hace que la comunicación sea más fluida y adaptable. Esto refleja una visión de la comunicación como un proceso dinámico y co - creado.")])]),_c('div',{staticClass:"col-md-6 col-lg-4"},[_c('img',{attrs:{"src":require("@/assets/curso/Tema1/img09.png"),"alt":""}})])])])],1)]),_c('separador'),_vm._m(7),_vm._m(8),_c('div',{staticClass:"row justify-content-center align-items-center mb-0"},[_c('div',{staticClass:"col-lg-12"},[_c('div',{staticClass:"fondo1 p-4"},[_c('div',{staticClass:"row justify-content-center align-items-center mb-1"},[_c('div',{staticClass:"col-lg-11"},[_c('SlyderA',{staticClass:"tarjetablanca p-4",attrs:{"tipo":"b"}},[_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-md-10 mb-4 mb-md-0 col-lg-6"},[_c('h5',{staticClass:"h5color"},[_vm._v("Actitud pasiva")]),_c('p',[_vm._v("La persona asume una actitud pasiva en el momento en que calla ante el maltrato que recibe, se suma a otra tesis, así no esté de acuerdo, no cree en sus conocimientos o no le es fiel a estos, se deja llevar por lo que digan los demás, le condiciona la baja autoestima, siendo esta una de las barreras más comunes en los procesos de comunicación.")])]),_c('div',{staticClass:"col-md-6 col-lg-4"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/Tema1/img12.png"),"alt":""}})])])]),_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-md-10 mb-4 mb-md-0 col-lg-6"},[_c('h5',{staticClass:"h5color"},[_vm._v("Actitud agresiva")]),_c('p',[_vm._v("Se asume una actitud agresiva, cuando la persona desde su cultura, su crianza y la forma de percibir al otro, piensa la violencia como recurso para solucionar problemas o sortear situaciones que estime van en contra de su lógica, dado que se aferra tanto a esta que desconoce otras formas de pensar.")])]),_c('div',{staticClass:"col-md-6 col-lg-4"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/Tema1/img13.png"),"alt":""}})])])]),_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-md-10 mb-4 mb-md-0 col-lg-6"},[_c('br'),_c('h5',{staticClass:"h5color"},[_vm._v("Actitud asertiva")]),_c('p',[_vm._v("La actitud asertiva frente a la comunicación denota contundencia enmarcada en el respeto por el otro desde la demarcación de límites en la relación, implica tolerar lo intolerable sin la expectativa de que el otro sea tolerante. La asertividad implica tranquilidad consigo mismo y con el otro, implica gestión de emociones y por tanto inteligencia emocional. La persona asertiva se da el tiempo para pensar, sentir y actuar, a la vez que sin aferrarse a sus impulsos acepta y comprende la diferencia del otro complejizando sus miradas en diversos contextos.")])]),_c('div',{staticClass:"col-md-6 col-lg-4"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/Tema1/img14.png"),"alt":""}})])])])])],1)])])])])],1)],1)
